import React, { useState } from "react";
import { Button } from "../button/page";

export const TicketLayout = () => {
  // Estado para controlar a visibilidade do modal
  const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <div className="px-10 py-6 flex flex-col">
      {/* Header */}
      <div className="flex justify-between items-center">
        <h1 className="font-montserrat text-2xl font-semibold text-gray-800">
          Chamados
        </h1>
        <div className="w-32 h-12">
          <Button
            text="New Desk"
            onClick={() => setIsModalOpen(true)} // Abre o modal ao clicar
          />
        </div>
      </div>

      {/* Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50">
          <div className="bg-white p-6 rounded-lg shadow-lg w-96">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">
              Criar Chamado
            </h2>
            <form
              onSubmit={(e) => {
                e.preventDefault();
                console.log("Chamado criado");
                setIsModalOpen(false); // Fecha o modal
              }}
            >
              {/* Formulário */}
              <div className="mb-4">
                <label
                  htmlFor="title"
                  className="block text-sm font-medium text-gray-700"
                >
                  Título
                </label>
                <input
                  type="text"
                  id="title"
                  className="mt-1 p-2 w-full border rounded-md"
                  required
                />
              </div>

              <div className="mb-4">
                <label
                  htmlFor="description"
                  className="block text-sm font-medium text-gray-700"
                >
                  Descrição
                </label>
                <textarea
                  id="description"
                  rows={4}
                  className="mt-1 p-2 w-full border rounded-md"
                  required
                />
              </div>

              <div className="flex justify-end gap-4">
                <button
                  type="button"
                  className="px-4 py-2 bg-gray-300 rounded-md text-gray-700"
                  onClick={() => setIsModalOpen(false)} // Fecha o modal
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 bg-cyan-600 text-white rounded-md hover:bg-cyan-700 transition"
                >
                  Criar
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};
